.reviews {
	padding-top : 32px;

	@media screen and (min-width : $break-table) {
		padding-top : 60px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.reviews__wrapper {
	position       : relative;
	padding-bottom : 100px;

	@media screen and (min-width : $break-table) {
		padding-bottom : 0;
	}
}

.reviews__head {
	display         : flex;
	align-items     : center;
	justify-content : space-between;
	padding         : 0 24px 24px 24px;
	@media screen and (min-width : $break-table) {
		padding : 0 32px 32px 32px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 0 48px 32px 48px;
	}
}

.reviews__main {
	border-top    : 1px solid var(--border_color);
	border-bottom : 1px solid var(--border_color);
	@media screen and (min-width : $break-table) {
		border-bottom : none;
	}
}

.reviews__wrap {
	display     : flex;
	align-items : center;
	gap         : 24px;

	.b-btn--primary {
		position  : absolute;
		z-index   : 1;
		bottom    : 0;
		left      : 50%;
		max-width : 280px;
		transform : translateX(-50%);

		@media screen and (min-width : $break-table) {
			position  : unset;
			left      : unset;
			min-width : 290px;
			max-width : none;
			padding   : 14px 32px;
			transform : translate3d(0, 0, 0);
		}

		@media screen and (min-width : $break-desktop) {

		}
	}


}

.reviews-item {
	position : relative;
	padding  : 24px 24px;
	@media screen and (min-width : $break-table) {
		padding : 32px 32px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 48px 48px;
	}
}

.reviews-item__top {
	display       : flex;
	margin-bottom : 24px;
	gap           : 20px;
	@media screen and (min-width : $break-table) {
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.reviews-item__img {
	width  : 64px;
	height : 64px;

	img {
		width      : 100%;
		height     : 100%;
		object-fit : cover;
	}
}

.reviews-item__name {
	font-size      : 20px;
	font-weight    : 400;
	line-height    : 140%;
	text-transform : uppercase;
	color          : var(--white);

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}

}

.reviews-item__meta {
	font-size   : 14px;
	font-weight : 400;
	font-style  : normal;
	line-height : 150%;
	margin-top  : 4px;
	color       : var(--white, #FFF);
}

.reviews-item__text {
	font-size   : 14px;
	font-weight : 400;
	line-height : 150%;
	opacity     : 0.8;
	color       : var(--white);

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 142%;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 137%;
	}
}

.swiper--reviews {


	@media screen and (max-width : $break-table-max) {
		&:before {
			display : none !important;
		}
	}

	.swiper-slide {
		width : 100%;

		@media screen and (min-width : $break-table) {
			width : 50%;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}
