.quiz__wrapper {
	border-top    : 1px solid var(--border_color);
	border-bottom : 1px solid var(--border_color);

	input[type="text"],
	input[type="email"],
	input[type="tel"] {
		border        : 0;
		border-bottom : 1px solid rgba(255, 255, 255, .1);
	}
}

.quiz__inner {
	padding-right : 24px;
	padding-left  : 24px;
	@media screen and (min-width : $break-table) {
		padding-right : 32px;
		padding-left  : 32px;
	}
	@media screen and (min-width : $break-desktop) {
		padding-right : 48px;
		padding-left  : 48px;
	}
}

.quiz__title {
	font-size      : 20px;
	font-weight    : 400;
	font-style     : normal;
	line-height    : 131.25%;
	max-width      : 868px;
	margin-bottom  : 24px;
	text-transform : uppercase;
	color          : var(--white, #FFF);
	@media screen and (min-width : $break-table) {
		font-size     : 28px;
		margin-bottom : 32px;
	}
	@media screen and (min-width : $break-desktop) {
		font-size     : 32px;
		margin-bottom : 48px;
	}
}

.quiz-start {
	@media screen and (min-width : $break-table) {
		display         : flex;
		align-items     : stretch;
		flex-flow       : row nowrap;
		justify-content : flex-start;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.quiz-start__main {
	box-sizing : border-box;
	padding    : 24px;
	@media screen and (min-width : $break-table) {
		@include grid-col(6);
		display         : flex;
		align-items     : flex-start;
		flex-flow       : column nowrap;
		justify-content : flex-start;
		box-sizing      : border-box;
		padding         : 47px 15px 0 47px;
	}
}

.quiz-start__content {
	margin-top  : auto;
	padding-top : 24px;
	@media screen and (min-width : $break-table) {
		padding-top : 32px;
	}
}

.quiz-start__image {
	display         : flex;
	align-items     : stretch;
	flex-flow       : row nowrap;
	justify-content : flex-start;
	gap             : 18px;
	@media screen and (min-width : $break-table) {
		order        : 1;
		@include grid-col(6);
		box-sizing   : border-box;
		padding-left : 15px;
		img {
			height         : 100%;
			vertical-align : top;
			object-fit     : cover;
		}
	}
}

.quiz-start__last {
	display         : flex;
	align-items     : stretch;
	flex-flow       : column nowrap;
	justify-content : flex-end;

	picture {
		display         : flex;
		align-items     : stretch;
		flex-flow       : column nowrap;
		justify-content : flex-start;
		max-height      : 60%;

		img {
			max-height : 100%;
		}
	}

	img {
		max-height : 60%;
	}
}

h2.quiz-start__title {
	font-size      : 24px;
	font-weight    : 400;
	font-style     : normal;
	line-height    : 131.25%;
	margin         : 0;
	text-transform : uppercase;
	color          : var(--white, #FFF);
	@media screen and (min-width : $break-table) {
		font-size : 28px;
		max-width : 632px;
	}
	@media screen and (min-width : $break-desktop) {
		font-size : 32px;
	}
}

.quiz-start__subtitle {
	font-size      : 24px;
	font-weight    : 400;
	font-style     : normal;
	line-height    : 133.333%;
	margin-bottom  : 0;
	text-transform : uppercase;
	color          : var(--white, #FFF);
	@media screen and (min-width : $break-table) {
		max-width : 269px;
	}
}

.quiz-start__description {
	font-size   : 16px;
	font-weight : 400;
	font-style  : normal;
	line-height : 137.5%;
	max-width   : 400px;
	margin-top  : 22px;
	color       : var(--white, #FFF);

	@media screen and (min-width : $break-table) {
		max-width : 348px;
	}
}

.quiz-start__button {
	margin-top : 24px;
	@media screen and (min-width : $break-table) {
		margin-top : 32px;
	}
	@media screen and (min-width : $break-desktop) {

	}
}
