.partners__head {
	display         : flex;
	align-items     : center;
	justify-content : space-between;
	padding         : 0 24px 24px 24px;
	@media screen and (min-width : $break-table) {
		padding : 0 32px 32px 32px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 0 48px 32px 48px;
	}
}

.partners__wrap {
	border-top    : 1px solid var(--border_color);
	border-bottom : 1px solid var(--border_color);
}

.swiper--partners {
	.swiper-wrapper {
		align-items : center;
	}

	.swiper-slide {
		position        : relative;
		display         : flex;
		align-items     : center;
		justify-content : center;
		width           : 50%;
		height          : 125px;
		padding         : 20px 20px;

		@media screen and (min-width : $break-table) {
			width  : 33.33333%;
			height : 157px;
		}

		@media screen and (min-width : $break-desktop) {
			width  : 25%;
			height : 208px;
		}

		@media screen and (min-width : $break-desktop-fh) {
			width  : 20%;
			height : 208px;
		}

		&:before {
			content          : "";
			position         : absolute;
			top              : 0;
			left             : 0;
			width            : 1px;
			height           : 100%;
			background-color : var(--border_color);
		}

		&:first-child {
			&:before {
				display : none;
			}
		}

	}
}
