.housing {
	padding-right : 24px;
	padding-left  : 24px;

	@media screen and (min-width : $break-table) {
		padding-right : 32px;
		padding-left  : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-right : 48px;
		padding-left  : 48px;
	}
}

.housing__head {
	display         : flex;
	align-items     : flex-end;
	justify-content : space-between;
	padding-bottom  : 24px;
	gap             : 24px;
	@media screen and (min-width : $break-table) {
		align-items    : center;
		padding-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.housing__wrapper {
	margin : 0 -24px;

	@media screen and (min-width : $break-table) {
		margin : 0 -10px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.product-card--housing {
	margin : 0 24px;

	@media screen and (min-width : $break-table) {
		margin : 0 10px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.swiper--housing {
	.swiper-slide {
		width : 100%;

		@media screen and (min-width : $break-table) {
			width : 50%;
		}

		@media screen and (min-width : $break-desktop) {
			width : 33.3333%;
		}
	}
}
