.page-wrapper {
	overflow   : clip;
	background : var(--dark);

	> .container {
		min-height : 100svh;
	}
}

.container {
	box-sizing : border-box;
	width      : (1440 + 60) * 1px;
	max-width  : 100%;
	margin     : auto;
	padding    : 0 16px;

	@media screen and (min-width : $break-table) {
		padding : 0 20px;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.section-title {
	font-size      : 24px;
	font-weight    : 400;
	line-height    : 133%;
	margin-bottom  : 0;
	text-transform : uppercase;
	color          : var(--white);

	@media screen and (min-width : $break-table) {
		font-size   : 28px;
		line-height : 135%;
	}
	@media screen and (min-width : $break-desktop) {
		font-size   : 32px;
		line-height : 131%;
	}
}

.container--main {
	position : relative;

	&:after {
		content          : "";
		position         : absolute;
		z-index          : 150;
		top              : 0;
		left             : 15px;
		width            : 1px;
		height           : 100%;
		background-color : var(--border_color);
		@media screen and (min-width : $break-table) {
			left : 19px;
		}
		@media screen and (min-width : $break-desktop) {

		}
	}

	&:before {
		content          : "";
		position         : absolute;
		z-index          : 150;
		top              : 0;
		right            : 15px;
		width            : 1px;
		height           : 100%;
		background-color : var(--border_color);

		@media screen and (min-width : $break-table) {
			right : 19px;
		}
		@media screen and (min-width : $break-desktop) {

		}
	}
}

.section-offset {
	padding : 32px 0;

	@media screen and (min-width : $break-table) {
		padding : 60px 0;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.scroll {
	@media screen and (max-width : $break-desktop) {
		position : relative;
		overflow : hidden;
		width    : 100vw;
		height   : 100vh;
	}
}

.site-main {
	padding : 30px 17px;
	@media screen and (min-width : $break-table) {
		padding : 40px 32px;
	}
	@media screen and (min-width : $break-desktop) {
		padding : 60px 48px;
	}

	.edit-link {
		display : none;
	}
}

.entry-content {
	h1, h2, h3, h4, h5, h6 {
		font-weight : 700;
	}

	h1 {
		font-size : 24px;
		@media screen and (min-width : $break-table) {
			font-size : 32px;
		}
		@media screen and (min-width : $break-desktop) {
			font-size : 38px;
		}
	}

	h2 {
		font-size : 20px;
		@media screen and (min-width : $break-table) {
			font-size : 24px;
		}
		@media screen and (min-width : $break-desktop) {
			font-size : 32px;
		}
	}

	h3 {
		font-size : 18px;
		@media screen and (min-width : $break-table) {
			font-size : 22px;
		}
		@media screen and (min-width : $break-desktop) {
			font-size : 28px;
		}
	}

	p {
		margin : 0 0 1em;

		&:last-child {
			margin-bottom : 0;
		}
	}
}

.entry-title {
	font-size      : 22px;
	font-weight    : 400;
	line-height    : 126%;
	text-transform : uppercase;
	color          : var(--white);
	@media screen and (min-width : 482px) {
		font-size : 30px;
	}
	@media screen and (min-width : $break-table) {
		font-size   : 36px;
		line-height : 122%;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 48px;
		line-height : 116%;
	}
}
