.hero {
	padding : 0 0 32px 0;

	@media screen and (min-width : $break-table) {
		padding : 0 0 60px 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.hero__wrapper {
	display               : grid;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.hero__title {
	font-size      : 30px;
	font-weight    : 400;
	line-height    : 126%;
	margin-bottom  : 0;
	padding-top    : 25px;
	padding-bottom : 24px;
	padding-left   : 24px;
	text-transform : uppercase;
	color          : var(--white);
	grid-column    : 4 span;

	@media screen and (min-width : $break-table) {
		font-size    : 36px;
		line-height  : 122%;
		padding-top  : 42px;
		padding-left : 32px;
		grid-column  : 1/8;
		grid-row     : 1/3;
	}

	@media screen and (min-width : $break-desktop) {
		font-size    : 48px;
		line-height  : 116%;
		padding-top  : 55px;
		padding-left : 48px;
	}
}

.hero__wrap {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		border-left : 1px solid var(--border_color);
		grid-column : 8/13;
		grid-row    : 1/3;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.hero__desc {
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 150%;
	margin-bottom : 24px;
	padding       : 0 24px;
	opacity       : 0.8;
	color         : var(--white);

	@media screen and (min-width : $break-table) {
		font-size     : 14px;
		line-height   : 137%;
		max-width     : 300px;
		margin-bottom : 32px;
		padding       : 32px 32px 0 32px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 16px;
		max-width     : 400px;
		margin-bottom : 106px;
		padding       : 42px 0 0 48px;
	}

	@media screen and (min-width : $break-desktop-fh) {
		max-width : 400px;
	}
}

.hero__image {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 1/13;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
