.accordion {
	padding-top   : 24px;
	border-bottom : 1px solid var(--border_color);

	@media screen and (min-width : $break-table) {
		padding-top : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&.open {
		.accordion__content {
			padding-bottom     : 24px;
			grid-template-rows : 1fr;

			@media screen and (min-width : $break-table) {
				padding-bottom : 32px;
			}

			@media screen and (min-width : $break-desktop) {
				padding-bottom : 52px;
			}
		}

		.accordion__icon {
			transform : rotate(180deg);
		}
	}
}

.accordion__content {
	display            : grid;
	padding-right      : 24px;
	padding-left       : 24px;
	transition         : grid-template-rows 500ms, padding-bottom 500ms;
	grid-template-rows : 0fr;

	@media screen and (min-width : $break-table) {
		padding : 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.accordion__main {
	overflow : hidden;


	@media screen and (min-width : $break-table) {
		display               : grid;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.accordion__control {
	position         : relative;
	display          : flex;
	justify-content  : space-between;
	width            : 100%;
	padding          : 0 24px 24px 86px;
	cursor           : pointer;
	border           : none;
	background-color : transparent;

	@media screen and (min-width : $break-table) {
		display               : grid;
		padding               : 0 0 24px 0;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		padding : 0 0 40px 0;
	}
}

.accordion__count {
	font-size         : 14px;
	font-weight       : 400;
	line-height       : 142%;
	position          : absolute;
	z-index           : 1;
	top               : 0;
	left              : 24px;
	counter-increment : count;
	opacity           : 0.8;
	color             : var(--white);

	@media screen and (min-width : $break-table) {
		left : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		left : 48px;
	}
}

.accordion__title {
	font-size      : 18px;
	font-weight    : 400;
	line-height    : 133%;
	max-width      : 186px;
	text-align     : start;
	text-transform : uppercase;
	color          : var(--white);

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 140%;
		max-width   : none;
		grid-column : 3/9;
	}

	@media screen and (min-width : 1440px) {
		transform : translateX(22px);
	}
}

.accordion__icon {
	width      : 20px;
	height     : 20px;
	transition : transform .3s ease;
	transform  : rotate(0deg);

	@media screen and (min-width : $break-table) {
		position    : relative;
		right       : -15px;
		width       : 24px;
		height      : 24px;
		grid-column : 12/13;
	}

	@media screen and (min-width : $break-desktop) {
		right : -51px;
	}

	img {
		width      : 100%;
		height     : 100%;
		object-fit : cover;
	}
}
