.about {

}

.about__wrapper {
	display               : grid;
	border-top            : 1px solid var(--border_color);
	border-bottom         : 1px solid var(--border_color);
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-template-columns : repeat(12, 1fr);
	}
}

.about__title {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 48px;
	}
}

.about__left {
	padding     : 24px 24px;
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		padding : 48px 32px 32px 32px;
	}

	@media screen and (min-width : $break-desktop) {
		padding     : 50px 0 0 48px;
		grid-column : 1/7;
	}
}

.about__images {
	display         : flex;
	align-items     : flex-start;
	justify-content : space-between;
	gap             : 9px;

	@media screen and (min-width : $break-table) {
		gap : 21px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	img {
		width      : 100%;
		height     : 100%;
		object-fit : contain;
	}
}

.about__img {

}

.about__desc {
	display         : flex;
	flex-direction  : column;
	justify-content : space-between;
	border-top      : 1px solid var(--border_color);
	grid-column     : 4 span;

	@media screen and (min-width : $break-table) {
		padding : 32px 32px;
	}

	@media screen and (min-width : $break-desktop) {
		padding     : 0;
		border-top  : none;
		border-left : 1px solid var(--border_color);
		grid-column : 8/13;
		grid-row    : 1/4;
	}

	.b-btn--primary {

		@media screen and (min-width : $break-table) {
			max-width : 353px;
		}

		@media screen and (min-width : $break-desktop) {
			max-width : none;
		}
	}
}

.about__text {
	font-size   : 14px;
	font-weight : 400;
	line-height : 150%;
	padding     : 24px 24px;
	opacity     : 0.8;
	color       : var(--white);

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 142%;
		padding     : 0 0 32px 0;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 137%;
		max-width   : 573px;
		padding     : 48px 48px 0 48px;
	}

	p {
		&:not(:last-child) {
			margin-bottom : 16px;

			@media screen and (min-width : $break-table) {
				margin-bottom : 25px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}
}
