.header {
	position : relative;
	z-index  : 100;
}

.header__wrapper {
	display         : flex;
	align-items     : center;
	justify-content : space-between;
	border-bottom   : 1px solid var(--border_color);
}

.header__logo {
	display         : flex;
	justify-content : center;
	padding         : 8px 24px;
	border-right    : 1px solid var(--border_color);

	@media screen and (min-width : $break-table) {
		padding : 12px 32px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 13px 48px;
	}

	a {
		display     : block;
		flex-shrink : 0;
	}

	img {
		width  : 48px;
		height : 48px;

		@media screen and (min-width : $break-table) {
			width  : 62px;
			height : 62px;
		}

		@media screen and (min-width : $break-desktop) {
			width  : 73px;
			height : 73px;
		}
	}
}

.header__panel {
	position         : absolute;
	z-index          : 10;
	top              : 65px;
	right            : 0;
	display          : flex;
	visibility       : hidden;
	flex-direction   : column;
	width            : 100%;
	height           : calc(100dvh - 61px);
	transition       : visibility .3s ease, opacity .3s ease;
	opacity          : 0;
	background-color : #141414;

	&.active {
		visibility : visible;
		opacity    : 1;
	}

	@media screen and (min-width : $break-table) {
		top    : 87px;
		height : calc(100dvh - 75px);
	}

	@media screen and (min-width : $break-desktop) {
		position   : unset;
		top        : 0;
		visibility : visible;
		height     : auto;
		padding    : 24px 48px;
		opacity    : 1;
	}
}

.header__menu {
	display        : flex;
	overflow-y     : auto;
	flex-direction : column;
	height         : 100%;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		overflow-y     : visible;
		flex-direction : row;
	}
}


.header__mobile {
	display               : grid;
	margin-top            : auto;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		display : none;
	}

	.b-btn--primary {
		grid-column : 4 span;
		grid-row    : 1/1;

		@media screen and (min-width : $break-table) {
			width       : 50%;
			grid-column : 4 span;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.header__contacts {
	padding       : 24px 24px;
	border-bottom : 1px solid var(--border_color);
	grid-column   : 4 span;

	@media screen and (min-width : $break-table) {
		padding       : 48px 32px;
		border-top    : 1px solid var(--border_color);
		border-bottom : 0;
		grid-column   : 2 span;

		&:nth-of-type(2) {
			border-left : 1px solid var(--border_color);
		}
		&:nth-of-type(4) {
			border-left : 1px solid var(--border_color);
		}
	}

	@media screen and (min-width : $break-desktop) {

	}

	.contact-name {
		font-size     : 12px;
		font-weight   : 400;
		line-height   : 150%;
		margin-bottom : 8px;
		opacity       : 0.5;
		color         : var(--White, #FFF);
	}

	.contact-address {
		font-size      : 18px;
		font-weight    : 400;
		line-height    : 133%;
		text-transform : uppercase;
		color          : var(--white);
	}

	a {
		font-size      : 18px;
		font-weight    : 400;
		line-height    : 133%;
		text-transform : uppercase;
		color          : var(--white);
	}
}

.nav__list {
	padding    : 38px 0 40px 24px;
	list-style : none;

	@media screen and (min-width : $break-table) {
		padding : 64px 0 40px 32px;
	}

	@media screen and (min-width : $break-desktop) {
		display : flex;
		padding : 0;
	}
}

.nav__item {
	position : relative;

	&:not(:last-child) {
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-table) {
		&:not(:last-child) {
			margin-bottom : 40px;
		}
	}

	@media screen and (min-width : $break-desktop) {
		&:not(:last-child) {
			margin-bottom : 0;
		}
		&:not(:first-child) {
			margin-left : 32px;


			&:before {
				content          : "";
				position         : absolute;
				top              : 50%;
				left             : -18px;
				width            : 4px;
				height           : 4px;
				transform        : translateY(-50%);
				border-radius    : 100%;
				background-color : var(--grey-dark);
			}
		}
	}


}

.nav__link {
	font-size   : 24px;
	font-weight : 400;
	line-height : 133%;
	position    : relative;
	color       : var(--white);

	@media screen and (min-width : $break-table) {
		font-size   : 36px;
		line-height : 122%;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 14px;
		line-height : 142%;

		&:before {
			content          : "";
			position         : absolute;
			z-index          : 1;
			bottom           : -2px;
			left             : 0;
			width            : 0;
			height           : 1px;
			transition       : width .3s ease;
			background-color : var(--white);
		}

		&:hover {
			&:before {
				width : 100%;
			}
		}
	}


}

.header__right {
	display     : flex;
	align-items : center;
	flex-shrink : 0;
	padding     : 24px 24px;
	border-left : 1px solid var(--border_color);

	@media screen and (min-width : $break-table) {
		padding : 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 33px 48px;
	}
}

.header__wrap {
	display     : flex;
	align-items : center;
	gap         : 36px;

	@media screen and (min-width : $break-table) {
		padding      : 33px 33px;
		border-right : 1px solid var(--border_color);
	}

	@media screen and (min-width : $break-desktop) {
		padding      : 0;
		border-right : 0;
	}
}

.header__number {
	font-size   : 14px;
	font-weight : 400;
	line-height : 142%;
	position    : relative;
	display     : none;
	color       : var(--white);
	@media screen and (min-width : $break-table) {
		display : inline-block;
	}

	@media screen and (min-width : $break-desktop) {
		line-height : 122%;
		&:before {
			content          : "";
			position         : absolute;
			z-index          : 1;
			bottom           : -2px;
			left             : 0;
			width            : 0;
			height           : 1px;
			transition       : width .3s ease;
			background-color : var(--white);
		}

		&:hover {
			&:before {
				width : 100%;
			}
		}
	}
}

.header__burger {
	display : flex;
	height  : 16px;

	@media screen and (min-width : $break-table) {
		padding : 0 32px;
	}

	@media screen and (min-width : $break-desktop) {
		display : none;
	}
}

.s-block {
	display     : flex;
	align-items : center;
}

.s-block--header {
	display : none;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		display : flex;
	}
}

.s-block--mobile {
	display : flex;
}

.s-block__link {
	position      : relative;
	display       : block;
	overflow      : hidden;
	width         : 32px;
	height        : 32px;
	border-radius : 50%;

	&::after,
	&::before {
		content       : "";
		position      : absolute;
		top           : 0;
		left          : 0;
		width         : 100%;
		height        : 100%;
		border-radius : 50%;
	}

	&::after {
		z-index    : 1;
		transition : opacity 0.3s cubic-bezier(0.7, 0, 0.2, 1);
		opacity    : 0;
		background : var(--white);
	}

	&::before {
		z-index    : 5;
		transition : transform 0.3s cubic-bezier(0.7, 0, 0.2, 1);
		background : var(--grey-dark);
	}

	&:not(:first-child) {
		margin-left : 8px;
	}

	svg {
		position : relative;
		z-index  : 10;

		circle {
			transition : opacity .3s ease;
			opacity    : 0.2;
			fill       : var(--white);
		}

		path {
			transition : fill .3s ease;
			fill       : var(--white);
		}

		rect {

		}
	}

	&:hover {
		&::before {
			transform : translate3d(0, -110%, 0);
		}

		&::after {
			opacity : 1;
		}

		svg {
			path {
				fill : var(--grey-dark);
			}
		}
	}
}

.logo {
	font-size   : 14px;
	font-weight : 500;
	line-height : 170%;
	color       : var(--white);

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 150%;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.burger {
	position         : relative;
	flex-shrink      : 0;
	width            : 24px;
	height           : 16px;
	padding          : 0;
	cursor           : pointer;
	color            : var(--white);
	border           : none;
	background-color : transparent;


	&::before,
	&::after {
		content          : "";
		position         : absolute;
		left             : 0;
		width            : 100%;
		height           : 1px;
		transition       : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		background-color : currentColor;
	}

	&::before {
		top : 0;
	}

	&::after {
		top : calc(100% - 1px);
	}

	&__line {
		position         : absolute;
		top              : 50%;
		left             : 0;
		width            : 100%;
		height           : 1px;
		transition       : transform 0.3s ease-in-out;
		transform        : translateY(-50%);
		border-radius    : 0;
		background-color : currentColor;
	}

	&--active {
		&::before {
			top        : 50%;
			transition : transform 0.3s ease-in-out, top 0.3s ease-in-out;
			transform  : rotate(45deg);
		}

		&::after {
			top        : 50%;
			transition : transform 0.3s ease-in-out, top 0.3s ease-in-out;
			transform  : rotate(-45deg);
		}

		.burger__line {
			transition : transform 0.3s ease-in-out;
			transform  : scale(0);
		}
	}
}
