label.checkbox-style,
.checkbox-style {
  color: #A7A8B1;
  cursor: pointer;
  display: block;
  font-size: 13px;
  font-weight: 400;
  line-height: 1.3;
  margin: 0;
  outline: 0;
  overflow: hidden;
  position: relative;
  -webkit-tap-highlight-color: transparent;
  user-select: none;
}
label.checkbox-style > span,
.checkbox-style > span {
  color: #A7A8B1;
  display: inline-block;
  padding: 2px 0 2px 28px;
  position: relative;
  vertical-align: top;
}
label.checkbox-style > span:before,
.checkbox-style > span:before {
  border: 1px solid #C1C1C1;
  border-radius: 3px;
  box-sizing: border-box;
  content: "";
  height: 20px;
  left: 0;
  position: absolute;
  top: 0;
  transition: border-color 0.3s ease, background-color 0.3s ease;
  width: 20px;
  z-index: 1;
}
label.checkbox-style > span:after,
.checkbox-style > span:after {
  background: #000;
  border-radius: 3px;
  content: "";
  height: 14px;
  left: 3px;
  opacity: 0;
  position: absolute;
  top: 3px;
  transition: opacity 0.3s ease;
  width: 14px;
  z-index: 5;
}
label.checkbox-style input[type=checkbox],
.checkbox-style input[type=checkbox] {
  height: 1px;
  left: -30px;
  outline: 0;
  position: absolute;
  -webkit-tap-highlight-color: transparent;
  top: 0;
  width: 1px;
}
label.checkbox-style input[type=checkbox]:checked ~ span:after,
.checkbox-style input[type=checkbox]:checked ~ span:after {
  opacity: 1;
}
label.checkbox-style input[type=checkbox].disabled ~ span:after, label.checkbox-style input[type=checkbox][disabled] ~ span:after,
.checkbox-style input[type=checkbox].disabled ~ span:after,
.checkbox-style input[type=checkbox][disabled] ~ span:after {
  background: #E3E3E3;
}

