.b-btn {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.25;
  position: relative;
  display: inline-flex;
  align-items: end;
  flex-flow: row nowrap;
  justify-content: space-between;
  box-sizing: border-box;
  width: 100%;
  padding: 24px 48px;
  user-select: none;
  transition: background 0.3s ease, color 0.3s ease;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
  text-decoration: none;
  border: 0;
  border-radius: 0;
}
.b-btn:hover, .b-btn:focus-within, .b-btn.focus {
  outline: 0;
}
.b-btn.disabled, .b-btn:disabled {
  cursor: default;
  opacity: 0.45;
  background: inherit;
  box-shadow: none;
}
.b-btn > svg {
  display: block;
  flex: 0 0 auto;
}
.b-btn > svg:first-child {
  margin-right: 6px;
}
.b-btn > svg:last-child {
  margin-left: 6px;
}
.b-btn--big {
  font-size: 18px;
  min-height: 59px;
  padding: 4px 30px;
}
.b-btn--small {
  font-size: 15px;
  min-height: 39px;
  padding: 4px 16px;
}
.b-btn--primary {
  overflow: hidden;
  padding: 24px 24px;
  color: var(--white);
  background: var(--white);
}
.b-btn--primary::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 110%;
  transition: transform 0.3s cubic-bezier(0.7, 0, 0.2, 1);
  background: var(--grey-dark);
}
.b-btn--primary span {
  position: relative;
  z-index: 10;
}
.b-btn--primary:hover, .b-btn--primary:focus-within, .b-btn--primary:active, .b-btn--primary.active {
  color: var(--dark);
  background: var(--white);
}
.b-btn--primary:hover::before, .b-btn--primary:focus-within::before, .b-btn--primary:active::before, .b-btn--primary.active::before {
  transform: translate3d(0, -110%, 0);
}
.b-btn--primary:hover:after, .b-btn--primary:focus-within:after, .b-btn--primary:active:after, .b-btn--primary.active:after {
  background-color: var(--dark);
}
.b-btn--primary:after {
  content: "";
  display: inline-block;
  width: 16px;
  height: 16px;
  margin-left: 8px;
  transition: background-color 0.3s ease;
  background-color: #FFF;
  background-repeat: no-repeat;
  background-size: cover;
  mask-image: url("../images/icon/arrow-up-right.svg");
  mask-position: 50% 50%;
  mask-repeat: no-repeat;
  mask-size: 100% 100%;
}
@media screen and (min-width: 743px) {
  .b-btn--primary {
    padding: 24px 32px;
  }
}
@media screen and (min-width: 1439px) {
  .b-btn--primary {
    padding: 24px 48px;
  }
}
.b-btn--loading:before {
  content: "";
  display: inline-block;
  width: 12px;
  height: 12px;
  margin-right: 10px;
  margin-left: -15px;
  transition: border 0.3s ease;
  animation: loadingSpin 1s infinite linear;
  vertical-align: middle;
  border: 3px solid rgba(255, 255, 255, 0.6);
  border-top-color: #FFF;
  border-radius: 50%;
}
.b-btn--loading.outline:before {
  border: 3px solid rgba(0, 0, 0, 0.2);
  border-top-color: #000;
}
.b-btn--loading.outline:hover:before {
  border: 3px solid rgba(255, 255, 255, 0.6);
  border-top-color: #FFF;
}
.b-btn--full-size {
  display: block;
  width: 100%;
}
.b-btn--full-size + .full-size {
  margin-right: 5px;
}
.b-btn + .b-btn {
  margin-left: 5px;
}

.b-btn-grouping {
  display: inline-flex;
}
.b-btn-grouping .b-btn {
  margin-right: 0;
  border-radius: 0;
}
.b-btn-grouping .b-btn:first-child {
  border-radius: 0 0 0 0;
}
.b-btn-grouping .b-btn:last-child {
  border-radius: 0 0 0 0;
}
.b-btn-grouping .b-btn.outline {
