.swiper {
  position: relative;
  z-index: 1;
  display: block;
  overflow: hidden;
  overflow: clip;
  margin-right: auto;
  margin-left: auto;
  padding: 0;
  list-style: none;
}

.swiper-vertical > .swiper-wrapper {
