}

.swiper-vertical {
  touch-action: pan-x;
}

.swiper-slide {
  position: relative;
  display: block;
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  transition-property: transform;
}

.swiper-slide-invisible-blank {
