.stages__title {
	padding : 0 24px 24px 24px;

	@media screen and (min-width : $break-table) {
		padding : 0 32px 32px 32px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 0 48px 48px 48px;
	}
}

.stages__text {
	font-size   : 14px;
	font-weight : 400;
	line-height : 150%;
	max-width   : 560px;
	opacity     : 0.5;
	color       : var(--white);

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 142%;
		max-width   : 500px;
		grid-column : 3/8;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 137%;
		max-width   : 614px;
		transform   : translateX(22px);
	}
}


