.magic-cursor {
	position       : absolute;
	z-index        : 99999;
	display        : none;
	visibility     : hidden;
	width          : 10px;
	height         : 10px;
	pointer-events : none;
	opacity        : 0;

	// Close
	&.is-close-enabled {
		opacity : 1 !important;
	}

	&.is-interactive-hover-on {
		z-index : -1 !important;
	}

	&.is-interactive-hover-on .magic-cursor__inner {
		overflow : hidden;
		border   : none !important;
	}
}

body[data-magic-cursor="body"] .magic-cursor {
	display    : block;
	visibility : visible;
}

.magic-cursor__inner {
	position        : fixed;
	display         : flex;
	align-items     : center;
	justify-content : center;
	pointer-events  : none;
	color           : var(--dark);
	border-radius   : 50%;
	background      : var(--white);

	// Drag
	.ball-drag {
		font-size   : 15px;
		font-weight : 600;
		line-height : 1.2;
		position    : absolute;
		display     : block;
		width       : 100%;
		padding     : 0 5px;
		transition  : all 0.3s;
		text-align  : center;
		color       : var(--white);

		&::before,
		&::after {
			content                : "";
			position               : absolute;
			top                    : 50%;
			display                : inline-block;
			width                  : 10px;
			height                 : 10px;
			margin-top             : -5px;
			transition             : all 0.25s;
			color                  : var(--white);
			text-rendering         : auto;
			-webkit-font-smoothing : antialiased;
		}

		&::before {
			left            : 0;
			transform       : translate3d(-30px, 0, 0);
			background      : url("../images/prev.svg") no-repeat 0 0;
			background-size : contain;
		}

		&::after {
			right           : 0;
			transform       : translate3d(30px, 0, 0);
			background      : url("../images/next.svg") no-repeat 0 0;
			background-size : contain;
		}
	}
}

.magnetic-wrap {
	position        : relative;
	display         : inline-flex;
	align-items     : center;
	justify-content : center;
	vertical-align  : top;
}

.magic-cursor__view {
	font-size      : 14px;
	font-weight    : 600;
	line-height    : 143%;
	position       : absolute;
	visibility     : hidden;
	padding        : 0 5px;
	transform      : scale(0);
	text-align     : center;
	text-transform : uppercase;
	opacity        : 0;
	color          : rgba(#0A0A0A, .6);

	html[lang="ru"] & {
		font-size : 12px;
	}
}

.magic-cursor__close {
	font-size   : 14px;
	font-weight : 600;
	line-height : 1;
	position    : absolute;
	padding     : 0 5px;
	text-align  : center;
	color       : var(--white);
}
