.footer__wrapper {
	display               : grid;
	padding               : 24px 24px;
	border-bottom         : 1px solid var(--border_color);
	grid-row-gap          : 24px;
	grid-template-columns : repeat(3, 1fr);
	@media screen and (min-width : $break-table) {
		padding               : 0 32px;
		border-top            : 1px solid var(--border_color);
		grid-row-gap          : 0;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		padding : 0;
	}
}

.footer__item {
	grid-column : 3 span;

	@media screen and (min-width : $break-table) {
		padding     : 32px 0;
		grid-column : 4 span;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 24px 44px;

		&:not(:first-child) {
			border-left : 1px solid var(--border_color);
		}
	}

	a {
		font-size   : 14px;
		font-weight : 400;
		line-height : 142%;
		position    : relative;
		color       : var(--white);
		@media screen and (min-width : $break-desktop) {
			&:before {
				content          : "";
				position         : absolute;
				z-index          : 1;
				bottom           : -2px;
				left             : 0;
				width            : 0;
				height           : 1px;
				transition       : width .3s ease;
				background-color : var(--white);
			}

			&:hover {
				&:before {
					width : 100%;
				}
			}
		}
	}

	&:last-child {

		@media screen and (min-width : $break-table) {
			text-align     : right;
			text-transform : uppercase;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.footer__copyright {
	font-size      : 14px;
	font-weight    : 400;
	line-height    : 150%;
	text-transform : uppercase;
	opacity        : 0.5;
	color          : var(--white);

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}
