.thanks-page {
	padding-top : 45px;
	@media screen and (min-width : $break-table) {
		padding-top : 90px;
	}
}

.thanks-page__wrapper {
	box-sizing    : border-box;
	width         : 100%;
	max-width     : 632px;
	margin-right  : auto;
	margin-left   : auto;
	padding-right : 16px;
	padding-left  : 16px;
	text-align    : center;
}

.thanks-page__title {
	font-size      : 24px;
	font-weight    : 400;
	font-style     : normal;
	line-height    : 116.667%;
	margin         : 0;
	text-align     : center;
	text-transform : uppercase;
	color          : var(--white, #FFF);
	@media screen and (min-width : $break-table) {
		font-size : 36px;
	}
	@media screen and (min-width : $break-desktop) {
		font-size : 48px;
	}
}

.thanks-page__button {
	margin-top   : 24px;
	margin-right : -16px;
	margin-left  : -16px;
	@media screen and (min-width : $break-table) {
		max-width    : 315px;
		margin-top   : 32px;
		margin-right : auto;
		margin-left  : auto;
	}
	@media screen and (min-width : $break-desktop) {
		max-width  : 100%;
		margin-top : 51px;
	}
}

.thanks-page__image {
	@media screen and (min-width : $break-table) {
		margin-top : 88px;
	}
	@media screen and (min-width : $break-desktop) {
		margin-top : 92px;
	}

	img {
		height     : 287px;
		object-fit : cover;
		@media screen and (min-width : $break-table) {
			height : 435px;
		}
		@media screen and (min-width : $break-desktop) {
			height : 508px;
		}
	}
}
