.loader {
	display    : none;
	visibility : hidden;
}


body.body--loader {
	overflow   : -moz-scrollbars-vertical;
	overflow-y : scroll;
	height     : 100vh;
	height     : 100svh;
	background : #000;

	.loader {
		position            : fixed;
		z-index             : 5000;
		top                 : 0;
		right               : 0;
		bottom              : 0;
		left                : 0;
		display             : block;
		visibility          : visible;
		pointer-events      : none;
		background          : var(--dark);
		overscroll-behavior : contain;
	}

	.page-wrapper {
		overflow : hidden;
		height   : 100vh;
		height   : 100svh;
	}
}

.loader__line {
	position    : absolute;
	top         : 0;
	left        : 0;
	width       : 0;
	height      : 8px;
	transition  : width .3s linear;
	background  : #D9D9D9;
	will-change : width;
}

.loader__status {
	font-size      : 64px;
	font-weight    : 400;
	font-style     : normal;
	line-height    : 1;
	position       : absolute;
	bottom         : 16px;
	left           : 16px;
	text-align     : center;
	text-transform : uppercase;
	color          : var(--White, #FFF);
	@media screen and (min-width : $break-table) {
		font-size : 80px;
		bottom    : 34px;
		left      : 34px;
	}
	@media screen and (min-width : $break-desktop) {
		font-size : 120px;
		bottom    : 64px;
		left      : 64px;
	}
}
