@keyframes mmslideIn {
	from {
		opacity : 0;
	}
	to {
		opacity : 1;
	}
}

@keyframes mmslideOut {
	from {
		opacity : 1;
	}
	to {
		opacity : 0;
	}
}

@keyframes mmslideInBg {
	from {
		opacity : 0;
	}
	to {
		opacity : .7;
	}
}

@keyframes mmslideOutBg {
	from {
		opacity : .7;
	}
	to {
		opacity : 0;
	}
}

.modal {
	display : none;
}

.modal.is-open {
	display : block;
}

.modal[aria-hidden="false"] .modal__overlay {
	animation : mmslideIn .3s ease;
}

.modal[aria-hidden="true"] .modal__overlay {
	animation : mmslideOut .3s ease;
	@media screen and (min-width : $break-table) {

	}
}

.modal__overlay {
	position                   : fixed;
	z-index                    : 8888;
	top                        : 0;
	right                      : 0;
	bottom                     : 0;
	left                       : 0;
	display                    : flex;
	overflow-y                 : auto;
	align-items                : normal;
	flex-flow                  : row nowrap;
	justify-content            : normal;
	box-sizing                 : border-box;
	padding                    : 8px;
	background                 : rgba(#0B0B0B, .9);
	-webkit-overflow-scrolling : touch;
	overscroll-behavior        : contain;

	_:-ms-fullscreen, :root & {
		display : block;
	}

	body.admin-bar & {
		padding-top    : 52px;
		padding-bottom : 52px;
	}

	@media screen and (min-width : $break-table) {
		padding : 16px;

	}

	.overflowscrolling & {
		overflow-y : scroll;
	}
}

.modal__container {
	position        : relative;
	z-index         : 20;
	box-sizing      : border-box;
	width           : 503px;
	max-width       : 100%;
	margin          : auto;
	padding         : 48px 16px;
	background      : var(--dark);
	background-size : cover;
	@media screen and (min-width : $break-table) {
		padding : 48px;
	}
}

.modal__content {

}

.modal__close {
	font-size                   : 18px;
	position                    : absolute;
	z-index                     : 300;
	top                         : 0;
	right                       : 0;
	display                     : flex;
	align-items                 : center;
	flex-flow                   : row nowrap;
	justify-content             : center;
	box-sizing                  : border-box;
	width                       : 48px;
	height                      : 48px;
	padding                     : 0;
	cursor                      : pointer;
	transition                  : opacity .3s ease;
	opacity                     : 1;
	border                      : 0;
	outline                     : 0;
	background                  : transparent;
	-webkit-tap-highlight-color : transparent;

	svg {
		display : block;
	}

	&:hover {
		opacity : .7;
	}
}

.modal__bg {
	position : fixed;
	z-index  : 1;
	top      : 0;
	right    : 0;
	bottom   : 0;
	left     : 0;
	cursor   : pointer;
}
