.contacts {
	padding-top : 64px;

	@media screen and (min-width : $break-table) {
		padding-top : 0;
	}

	@media screen and (min-width : $break-desktop) {

	}

}

.contacts__wrapper {
	display               : grid;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		border-top            : 1px solid var(--border_color);
		grid-template-columns : repeat(12, 1fr);
	}
}

.contacts__left {
	display        : flex;
	flex-direction : column;
	padding        : 24px 24px;
	border-bottom  : 1px solid var(--border_color);
	grid-column    : 4 span;

	@media screen and (min-width : $break-table) {
		padding       : 48px 32px;
		border-bottom : none;
		grid-column   : 2 span;
		grid-row      : 2/2;
	}

	@media screen and (min-width : $break-desktop) {
		padding     : 48px 0 34px 48px;
		grid-column : 4 span;
		grid-row    : unset;
	}
}

.contacts__title {
	margin-bottom : 14px;
}

.contacts__text {
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 150%;
	margin-bottom : 24px;
	opacity       : 0.8;
	color         : var(--white);

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 142%;
		max-width   : 343px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 137%;
	}

}

.contacts__socials {
	margin-top : auto;

	p {
		font-size     : 14px;
		font-weight   : 400;
		line-height   : 150%;
		margin-bottom : 16px;
		opacity       : 0.7;
		color         : var(--white);

		@media screen and (min-width : $break-table) {
			font-size   : 14px;
			line-height : 142%;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 16px;
			line-height : 137%;
		}
	}
}

.contacts__img {
	grid-column : 4 span;
	grid-row    : 1/1;

	@media screen and (min-width : $break-desktop) {
		grid-row : unset;
	}

	img {
		width      : 100%;
		height     : 100%;
		object-fit : cover;
	}
}

.form--contacts {
	display        : flex;
	flex-direction : column;
	height         : 100%;

	.form__wrapper {
		display        : flex;
		flex-direction : column;
		padding        : 24px 24px;

		@media screen and (min-width : $break-table) {
			padding : 48px 22px;
		}

		@media screen and (min-width : $break-desktop) {
			padding : 48px 48px 40px 48px;
		}
	}

	.b-btn--primary {
		margin-top : auto;
	}
}

.contacts__form {
	grid-column : 4 span;
	grid-row    : 3/3;

	@media screen and (min-width : $break-table) {
		border-left : 1px solid var(--border_color);
		grid-column : 2 span;
		grid-row    : 2/2;
	}

	@media screen and (min-width : $break-desktop) {
		border-left : none;
		grid-column : 4 span;
		grid-row    : unset;
	}

	> .wpcf7 {
		display         : flex;
		align-items     : stretch;
		flex-flow       : column nowrap;
		justify-content : flex-start;
		height          : 100%;

		> .wpcf7-form {
			display         : flex;
			align-items     : stretch;
			flex-flow       : column nowrap;
			justify-content : flex-start;
			height          : 100%;

			> p {
				margin-top : auto;
			}
		}
	}
}

.contacts__grid {
	display               : grid;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-template-columns : repeat(12, 1fr);
	}
}

.contacts__item {
	padding       : 24px 24px;
	border-bottom : 1px solid var(--border_color);
	grid-column   : 4 span;

	@media screen and (min-width : $break-table) {
		padding       : 48px 32px;
		border-top    : 1px solid var(--border_color);
		border-bottom : none;
		grid-column   : 2 span;

		&:not(:first-child) {
			border-left : 1px solid var(--border_color);
		}

		&:last-child {
			border-left : 0;
			grid-column : 4 span;
		}
	}

	@media screen and (min-width : $break-desktop) {
		padding       : 48px 44px;
		border-bottom : 1px solid var(--border_color);
		grid-column   : 4 span;

		&:last-child {
			border-left : 1px solid var(--border_color);
		}
	}


	a {
		font-size      : 18px;
		font-weight    : 400;
		line-height    : 133%;
		position       : relative;
		text-transform : uppercase;
		color          : var(--White, #FFF);
		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 20px;
			line-height : 140%;

			&:not(.s-block__link):before {
				content          : "";
				position         : absolute;
				z-index          : 1;
				bottom           : -2px;
				left             : 0;
				width            : 0;
				height           : 1px;
				transition       : width .3s ease;
				background-color : var(--white);
			}

			&:hover {
				&:before {
					width : 100%;
				}
			}
		}
	}
}

.contacts__name {
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 150%;
	margin-bottom : 8px;
	opacity       : 0.5;
	color         : var(--white);

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 142%;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.contacts__address {
	font-size      : 18px;
	font-weight    : 400;
	line-height    : 133%;
	text-transform : uppercase;
	color          : var(--White, #FFF);

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 140%;
	}
}

.contacts__partners {
	display    : flex;
	margin-top : 48px;
}

.partner {
	display   : flex;
	max-width : 138px;
}
