.services-item {
	padding          : 24px 24px;
	border-bottom    : 1px solid var(--border_color);
	background-color : var(--dark);

	&:first-child {
		border-top : 1px solid var(--border_color);
	}

	@media screen and (min-width : $break-table) {
		border : 1px solid var(--border_color);

		&:not(:last-child) {
			margin-bottom : 24px;
		}
	}

	@media screen and (min-width : $break-desktop) {
		padding      : 32px 32px;
		border-right : none;


		&:not(:last-child) {
			margin-bottom : 28px;
		}
	}

	@media screen and (min-width : 1440px) {
		position : absolute;
		z-index  : 1;
		right    : 0;
		width    : 820px;
		height   : 648px;
	}

	.b-btn--primary {
		padding : 20px 24px;

		@media screen and (min-width : $break-table) {
			padding : 20px 32px;
		}

		@media screen and (min-width : $break-desktop) {
			max-width : 293px;
		}
	}
}

.services-item__img {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		height : 349px;
	}

	img {
		width      : 100%;
		height     : 100%;
		object-fit : cover;
	}
}


.services-item__title {
	font-size      : 18px;
	font-weight    : 400;
	line-height    : 133%;
	margin-bottom  : 16px;
	text-transform : uppercase;
	color          : var(--white);

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 140%;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.services-item__desc {
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 150%;
	margin-bottom : 24px;
	opacity       : 0.5;
	color         : var(--white);

	@media screen and (min-width : $break-table) {
		font-size     : 14px;
		line-height   : 142%;
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 137%;
	}

	@media screen and (min-width : $break-desktop-fh) {
		max-width : 601px;
	}
}


