// -----------------------------------------------
//  Form config
// -----------------------------------------------
// Error
$form-label-color  : #A7A8B1;
$form-label-size   : 12px;
$form-label-weight : 400;
$form-label-margin : 0 0 10px;
$form-error-color  : var(--red);
$form-error-size   : 13px;
$form-error-weight : 500;

// Hint
$form-hint-color   : #444;
$form-hint-size    : 12px;
$form-hint-weight  : 400;
$form-hint-style   : italic;
$form-hint-margin  : 5px 0 0;

// Grid
$form-grid         : 7px;

// -----------------------------------------------
//  Form style
// -----------------------------------------------
.form__row {
	margin-top : 26px;
	position   : relative;

	&:first-child {
		margin-top : 0;
	}

	input[type="text"],
	input[type="email"],
	input[type="search"],
	input[type="number"],
	input[type="tel"],
	input[type="password"],
	select,
	textarea {
		vertical-align : top;
		width          : 100%;
	}

