}

.form__input {
	border        : none;
	border-bottom : 1px solid rgba(255, 255, 255, 0.1);
	position      : relative;
	z-index       : 1;
}


.form__area {
	border        : none;
	border-bottom : 1px solid rgba(255, 255, 255, 0.1);
	resize        : none;
}

.form__hint {
	color       : $form-hint-color;
	font-size   : $form-hint-size;
	font-style  : $form-hint-style;
	font-weight : $form-hint-weight;
	margin      : $form-hint-margin;
	position    : relative;
	z-index     : 2;
}

.wpcf7-not-valid-tip,
.form__error {
	color       : #D22D2D;
	font-size   : 14px;
	font-style  : normal;
	font-weight : 400;
	line-height : 20px;
	margin-top  : 10px;
}

.form__label {
	display        : flex;
	flex-direction : column;
}

.form-password {
	position : relative;

	input[type="text"],
	input[type="password"] {
		padding-right : 50px;
		position      : relative;
		z-index       : 1;
	}
}

.is-error.just-validate-error-label {
	color       : #D22D2D !important;
	font-size   : 14px;
	font-style  : normal;
	font-weight : 400;
	line-height : 20px;
	margin-top  : 10px;
}

.form-password__button {
	background                  : transparent;
	border                      : 0;
	bottom                      : 0;
	box-sizing                  : border-box;
	cursor                      : pointer;
	opacity                     : .3;
	outline                     : 0;
	padding                     : 0;
	position                    : absolute;
	right                       : 0;
	-webkit-tap-highlight-color : transparent;
	top                         : 0;
	transition                  : opacity .3s ease;
	vertical-align              : middle;
	width                       : 42px;
	z-index                     : 10;

	i {
		position       : relative;
		vertical-align : middle;
		z-index        : 1;
	}

	&:after {
		background : #000;
		border-top : 1px solid #FFF;
		content    : "";
		display    : block;
		height     : 1px;
		left       : 9px;
		position   : absolute;
		top        : 24px;
		transform  : rotate(-30deg);
		transition : opacity .3s ease;
		width      : 24px;
		z-index    : 10;
	}

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		&:hover {
			opacity : 1;
		}
	}

	&.active {
		opacity : 1;

		&:after {
			opacity : 0;
		}
	}
}

.wpcf7 form .wpcf7-response-output {
	margin      : 14px 0;
	font-size   : 14px;
	padding     : 10px 14px;
	line-height : 1.3;
}
