.product-card {
	display                     : flex;
	flex-direction              : column;
	box-sizing                  : border-box;
	width                       : calc(100% - 48px);
	padding                     : 0;
	text-decoration             : none;
	border                      : 0;
	outline                     : 0;
	background                  : transparent;
	-webkit-tap-highlight-color : transparent;
	@extend .cursor-drag;
	@media screen and (min-width : $break-table) {
		width : calc(100% - 24px);
	}

	&:hover {
		.product-card__img {
			&::after {
				opacity : .5;
			}

			img {
				transform : scale(1.05);
			}
		}
	}
}

.product-card__img {
	position      : relative;
	display       : block;
	overflow      : hidden;
	width         : 100%;
	margin-bottom : 24px;

	&::after {
		content    : "";
		position   : absolute;
		z-index    : 10;
		top        : 0;
		right      : 0;
		bottom     : 0;
		left       : 0;
		transition : opacity 0.6s ease;
		opacity    : 0;
		background : #000;
	}

	img {
		position   : relative;
		z-index    : 1;
		display    : block;
		width      : 100%;
		transition : transform 0.6s ease;
		object-fit : cover;
	}
}

.product-card__name {
	font-size      : 18px;
	font-weight    : 400;
	line-height    : 133%;
	position       : relative;
	display        : block;
	width          : 100%;
	text-align     : left;
	text-transform : uppercase;
	color          : var(--white);
	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 140%;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&:before {
		content           : "";
		position          : absolute;
		top               : 0;
		right             : 0;
		width             : 16px;
		height            : 16px;
		background-image  : url("../images/icon/arrow-up-right.svg");
		background-repeat : no-repeat;
		background-size   : cover;
	}
}
