.team {
	padding-right : 24px;
	padding-left  : 24px;

	@media screen and (min-width : $break-table) {
		padding-right : 32px;
		padding-left  : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-right : 48px;
		padding-left  : 48px;
	}
}

.team__head {
	display         : flex;
	align-items     : flex-end;
	justify-content : space-between;
	padding-bottom  : 24px;

	@media screen and (min-width : $break-table) {
		align-items    : center;
		padding-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.team__wrapper {
	margin : 0 -24px;

	@media screen and (min-width : $break-table) {
		margin : 0 -10px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.team-card {
	margin : 0 24px;

	@media screen and (min-width : $break-table) {
		margin : 0 10px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.team-card__img {
	margin-bottom : 24px;

	img {
		width      : 100%;
		object-fit : cover;
	}
}

.team-card__name {
	font-size      : 18px;
	font-weight    : 400;
	line-height    : 133%;
	margin-bottom  : 8px;
	text-transform : uppercase;
	color          : var(--white);

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 140%;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.team-card__job {
	font-size   : 14px;
	font-weight : 400;
	font-style  : normal;
	line-height : 150%;
	color       : var(--white);

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 142%;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.swiper--team {
	.swiper-slide {
		width : 100%;

		@media screen and (min-width : $break-table) {
			width : 50%;
		}

		@media screen and (min-width : $break-desktop) {
			width : 33.3333%;
		}
	}
}
