// -----------------------------------------------
//  Button config
// -----------------------------------------------

$btn-font-weight           : 400;
$btn-line-height           : 1.25;
$btn-border-width          : 1px;
$btn-padding               : 24px 48px;
$btn-font-size             : 14px;
$btn-border-radius         : 0;
$btn-transition            : background .3s ease, color .3s ease;
$btn-height                : 49px;

// OUTLINE

// SIZE
// Big
$btn-padding-big           : 4px 30px;
$btn-font-size-big         : 18px;
$btn-height-big            : 59px;

// Small
$btn-padding-small         : 4px 16px;
$btn-font-size-small       : 15px;
$btn-height-small          : 39px;

// COLOR
// White
$btn-white-bg              : var(--grey-dark);
$btn-white-color           : var(--white);
$btn-white-bg-hover        : var(--white);
$btn-white-color-hover     : var(--dark);

// LOADING
$btn-loading-color         : #FFF;
$btn-loading-color-outline : #000;
// SPACING
$btn-spacing-x             : 5px;
$btn-spacing-y             : 5px;

// -----------------------------------------------
//  Button style
// -----------------------------------------------

.b-btn {
	font-size       : $btn-font-size;
	font-weight     : $btn-font-weight;
	line-height     : $btn-line-height;
	position        : relative;
	display         : inline-flex;
	align-items     : end;
	flex-flow       : row nowrap;
	justify-content : space-between;
	box-sizing      : border-box;
	width           : 100%;
	padding         : $btn-padding;
	user-select     : none;
	transition      : $btn-transition;
	text-align      : center;
	vertical-align  : middle;
	white-space     : nowrap;
	text-decoration : none;
	border          : 0;
	border-radius   : $btn-border-radius;

	&:hover,
	&:focus-within,
	&.focus {
		outline : 0;
	}

	&.disabled,
	&:disabled {
		cursor     : default;
		opacity    : .45;
		background : inherit;
		box-shadow : none;
	}

	&:active,
	&.active {

	}

	> svg {
		display : block;
		flex    : 0 0 auto;

		&:first-child {
			margin-right : 6px;
		}

		&:last-child {
			margin-left : 6px;
		}
	}

	// SIZE
	// Big
	&--big {
		font-size  : $btn-font-size-big;
		min-height : $btn-height-big;
		padding    : $btn-padding-big;
	}

	&--small {
		font-size  : $btn-font-size-small;
		min-height : $btn-height-small;
		padding    : $btn-padding-small;
	}

	// COLOR
	// Primary
	&--primary {
		overflow   : hidden;
		padding    : 24px 24px;
		color      : $btn-white-color;
		background : $btn-white-bg-hover;

		&::before {
			content    : "";
			position   : absolute;
			top        : 0;
			left       : 0;
			width      : 100%;
			height     : 110%;
			transition : transform 0.3s cubic-bezier(0.7, 0, 0.2, 1);
			background : $btn-white-bg;
		}

		span {
			position : relative;
			z-index  : 10;
		}

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			color      : $btn-white-color-hover;
			background : $btn-white-bg-hover;

			&::before {
				transform : translate3d(0, -110%, 0);
			}

			&:after {
				background-color : var(--dark);
			}
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {

			}
		}

		&:after {
			content           : "";
			display           : inline-block;
			width             : 16px;
			height            : 16px;
			margin-left       : 8px;
			transition        : background-color .3s ease;
			background-color  : #FFF;
			background-repeat : no-repeat;
			background-size   : cover;
			mask-image        : url("../images/icon/arrow-up-right.svg");
			mask-position     : 50% 50%;
			mask-repeat       : no-repeat;
			mask-size         : 100% 100%;
		}

		@media screen and (min-width : $break-table) {
			padding : 24px 32px;
		}
		@media screen and (min-width : $break-desktop) {
			padding : 24px 48px;
		}
	}

	// LOADING
	&--loading {
		&:before {
			content          : "";
			display          : inline-block;
			width            : 12px;
			height           : 12px;
			margin-right     : 10px;
			margin-left      : -15px;
			transition       : border .3s ease;
			animation        : loadingSpin 1s infinite linear;
			vertical-align   : middle;
			border           : 3px solid rgba($btn-loading-color, 0.6);
			border-top-color : $btn-loading-color;
			border-radius    : 50%;
		}

		&.outline {
			&:before {
				border           : 3px solid rgba($btn-loading-color-outline, 0.2);
				border-top-color : $btn-loading-color-outline;
			}

			&:hover {
				&:before {
					border           : 3px solid rgba($btn-loading-color, 0.6);
					border-top-color : $btn-loading-color;
				}
			}
		}
	}

	// FULL-SIZE
	&--full-size {
		display : block;
		width   : 100%;

		+ .full-size {
			margin-right : $btn-spacing-y;
		}
	}

	// SPACING
	& + .b-btn {
		margin-left : $btn-spacing-x;
	}
}

.b-btn-grouping {
	display : inline-flex;

	.b-btn {
		margin-right  : 0;
		border-radius : 0;

		&:first-child {
			border-radius : $btn-border-radius 0 0 $btn-border-radius;
		}

		&:last-child {
			border-radius : 0 $btn-border-radius $btn-border-radius 0;
		}

		&.outline {
			z-index     : 1;
			margin-left : -1px;

			&:hover {
				z-index : 2;
			}

			&.active {
				z-index : 3;
			}

			&:first-child {
				margin-left : 0;
			}
		}
	}
}

@keyframes loadingSpin {
	0% {
		transform : rotate(0deg);
	}
	100% {
		transform : rotate(360deg);
	}
}

// Future-proof disabling of clicks on `<a>` elements
a.b-btn.disabled,
fieldset[disabled] a.b-btn {
	pointer-events : none;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
	&.full-size {
		width : 100%;
	}
}
