.tabs__content {
	display : none;

	&.active {
		display : block;
	}
}

.tabs__buttons {
	position         : absolute;
	z-index          : 3;
	left             : 50%;
	width            : 100%;
	max-height       : 321px;
	margin           : 1px 0 0 0;
	padding          : 0;
	list-style       : none;
	transition       : transform .3s ease, opacity .3s ease, color .3s ease, background-color .3s ease;
	transform        : scaleY(0) translateX(-50%);
	transform-origin : 0 0;
	opacity          : 0;
	background-color : var(--dark);

	@media screen and (min-width : $break-table) {
		position   : unset;
		max-height : none;
		margin     : 0;
		transform  : unset;
		opacity    : 1;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.tabs__button {
	position    : relative;
	display     : flex;
	overflow    : hidden;
	align-items : center;
	width       : 100%;
	padding     : 0;
	cursor      : pointer;
	border      : none;
	background  : transparent;
	gap         : 12px;
	@media screen and (min-width : $break-table) {
		background : var(--grey-dark);
		&::before {
			content    : "";
			position   : absolute;
			top        : 0;
			left       : 0;
			width      : 100%;
			height     : 110%;
			transition : transform 0.3s cubic-bezier(0.7, 0, 0.2, 1);
			background : var(--dark);
		}
	}

	svg {
		position   : relative;
		z-index    : 10;
		display    : block;
		transition : opacity .3s ease;
		opacity    : 0.5;
	}

	span {
		font-size   : 14px;
		font-weight : 400;
		font-style  : normal;
		line-height : 150%;
		position    : relative;
		z-index     : 10;
		transition  : opacity .3s ease;
		opacity     : 0.5;
		color       : var(--white);

		@media screen and (min-width : $break-table) {
			font-size   : 14px;
			line-height : 142%;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&:hover {
		&::before {
			transform : translate3d(0, -110%, 0);
		}

		img,
		span {
			opacity : 1;
		}

		svg {
			g {
				opacity : 1;
			}
		}
	}

	svg {
		g {
			transition : opacity .3s ease;
			opacity    : 0.5;
		}
	}

	&.active {
		&::before {
			transform : translate3d(0, -110%, 0);
		}

		img,
		span {
			opacity : 1;
		}

		svg {
			g {
				opacity : 1;
			}
		}
	}
}

.tabs__head {
	position      : relative;
	height        : 66px;
	border-top    : 1px solid var(--border_color);
	border-bottom : 1px solid var(--border_color);

	@media screen and (min-width : $break-table) {
		border-top    : none;
		border-bottom : none;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&.active {
		.tabs__buttons {
			transform     : scaleY(1) translateX(-50%);
			opacity       : 1;
			border-top    : none;
			border-bottom : 1px solid var(--border_color);
		}

		.tabs__selected {
			&:before {
				transform : rotate(180deg);
			}
		}
	}
}

.tabs__selected {
	font-size   : 14px;
	font-weight : 400;
	line-height : 150%;
	position    : relative;
	display     : flex;
	align-items : center;
	gap         : 12px;

	svg {
		g {
			opacity : 1;
		}
	}

	&:before {
		content           : "";
		position          : absolute;
		z-index           : 1;
		top               : 4px;
		right             : 0;
		width             : 20px;
		height            : 20px;
		transition        : transform .3s ease;
		transform         : rotate(0deg);
		background-image  : url(../images/icon/chevron-down.svg);
		background-repeat : no-repeat;
		background-size   : cover;
	}
}

.tabs__text {
	padding : 20px 24px;

	@media screen and (min-width : $break-table) {
		display : none;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.tabs__item {
	padding : 20px 24px;


	&:not(:last-child) {
		border-bottom : 1px solid var(--border_color);
	}

	@media screen and (min-width : $break-table) {
		padding : 0;
		&:not(:last-child) {
			border-bottom : 0;
		}
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.tabs--services {
	position              : relative;
	display               : grid;
	align-items           : flex-start;

	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : 1440px) {
		height : 664px;
	}

	.section-title {
		margin-bottom : 24px;
		padding-left  : 24px;
		grid-column   : 4 span;

		@media screen and (min-width : $break-table) {
			margin-bottom : 30px;
			padding-left  : 35px;
			grid-column   : 1/6;
			grid-row      : 1/1;
		}

		@media screen and (min-width : $break-desktop) {
			padding-left : 48px;
			grid-row     : 1/2;
		}

		@media screen and (min-width : 1440px) {
			grid-row : 1/9;
		}
	}

	.tabs__head {
		position         : sticky;
		z-index          : 5;
		top              : 0;
		left             : 0;
		background-color : var(--dark);
		grid-column      : 4 span;

		@media screen and (min-width : $break-table) {
			position    : unset;
			grid-column : 1/6;
			grid-row    : 2/4;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 1/5;
			grid-row    : 2/6;
		}

		@media screen and (min-width : 1440px) {
			grid-column : 1/4;
		}
	}

	.tabs__buttons {
		@media screen and (min-width : $break-table) {
			border-top    : 1px solid var(--border_color);
			border-right  : 1px solid var(--border_color);
			border-bottom : 1px solid var(--border_color);
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	.tabs__button {


		@media screen and (min-width : $break-table) {
			padding : 15px;
		}

		@media screen and (min-width : $break-desktop) {
			padding : 22px 15px 22px 58px;
		}
	}

	.tabs__item {
		@media screen and (min-width : $break-table) {
			&:not(:last-child) {
				border-bottom : 1px solid var(--border_color);
			}
		}

		@media screen and (min-width : $break-desktop) {

		}

	}

	.tabs__content {
		grid-column : 4 span;

		@media screen and (min-width : $break-table) {
			margin-right : 32px;
			margin-left  : 42px;
			grid-column  : 6/13;
			grid-row     : 1/5;
		}

		@media screen and (min-width : $break-desktop) {
			margin-right : 0;
			margin-left  : 0;
		}
	}
}

.tabs--stages {
	.tabs__buttons {
		@media screen and (min-width : $break-table) {
			display               : grid;
			align-items           : center;
			justify-content       : center;
			border-top            : 1px solid var(--border_color);
			border-bottom         : 1px solid var(--border_color);
			grid-template-columns : repeat(12, 1fr);
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	.tabs__item {
		@media screen and (min-width : $break-table) {
			grid-column : 6 span;
		}

		@media screen and (min-width : $break-desktop) {

		}

		&:not(:first-child) {
			border-left : 1px solid var(--border_color);
		}
	}

	.tabs__button {
		@media screen and (min-width : $break-table) {
			justify-content : center;
			width           : 100%;
			padding         : 22px 0;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	.tabs__content {
		counter-reset : count;
	}
}
